import React, { Component } from "react";
import Dropzone from 'react-dropzone';

import './dnd.css'

export default class Dnd extends Component {

    render() {
        return (
            <Dropzone onDrop={acceptedFiles => console.log(acceptedFiles)}>
                {({getRootProps, getInputProps}) => (
                    <section className="w-100 h-100 border border-dark rounded dnd-border">
                        <div {...getRootProps()}  className="w-100 h-100 mx-auto my-auto  row">
                            <input {...getInputProps()} />
                            <div className="col   mx-auto my-auto">Also you can drag 'n' drop text files here, or click to select files</div>
                        </div>
                    </section>
                )}
            </Dropzone>
        );
    }
}
